// import { SagaIterator } from 'redux-saga';
import { call, put, takeEvery } from 'redux-saga/effects';

import { GET_ALL_USER } from './constants';
import { getAllUserDone } from './action';
import { getAllUserApi } from './helpers';
import { toast } from 'react-toastify';
import { logoutDone } from '../auth/action';

function* takeGetAllUserSaga(action: any) {
  const res = yield call(getAllUserApi,action.data);
  if(res.data && res.data.list ){
    action.onSuccess(res.data)
    yield put(getAllUserDone(res.data));
  } else {
    if(res.error === 2){
      yield put(logoutDone())
    }
    toast.error(res.messages)
  }
}

export default function*() {
  yield takeEvery(GET_ALL_USER, takeGetAllUserSaga);
}
