export async function searchUserApi(payload: Partial<any>): Promise<any> {
  const res = await sapApi.get(sapApiMap.search_user_by_option+`?option=name&param=${payload.query}`);
  if (get(res, 'status') === 200) return res.data;
  return {
    data: []
  }
}