import { CREATE_ROLE, GET_ALL_ROLES, GET_ALL_ROLES_DONE, GET_ALL_SUB_ROLES, GET_ALL_SUB_ROLES_DONE, GET_DETAIL_ROLES, GET_DETAIL_ROLES_DONE, UPDATE_ROLE } from './constants';
export const getAllRoles = (data: any, onSuccess: any, onError: any) => ({
    type: GET_ALL_ROLES,
    data,
    onSuccess,
    onError
})

export const getAllRolesDone = (data: any) => ({
    type: GET_ALL_ROLES_DONE,
    data
})

export const getAllSubRoles = (data: any, onSuccess: any, onError: any) => ({
    type: GET_ALL_SUB_ROLES,
    data,
    onSuccess,
    onError
})

export const getAllSubRolesDone = (data: any) => ({
    type: GET_ALL_SUB_ROLES_DONE,
    data
})

export const getDetailRole = (data: any, onSuccess: any, onError: any) => ({
    type: GET_DETAIL_ROLES,
    data,
    onSuccess,
    onError
})

export const getDetailRoleDone = (data: any) => ({
    type: GET_DETAIL_ROLES_DONE,
    data
})

export const createRole = (data: any, onSuccess: any, onError: any) => ({
    type: CREATE_ROLE,
    data,
    onSuccess,
    onError
})

export const updateRole = (data: any, onSuccess: any, onError: any) => ({
    type: UPDATE_ROLE,
    data,
    onSuccess,
    onError
})