import { GET_ALL_ROLES_DONE } from "./constants";
import { fromJS } from 'immutable';

export const initialState = fromJS({
    listRoles: []
});

const BaoCaoReducer = (state = initialState, action: any) : any => {
    switch (action.type) {
        case GET_ALL_ROLES_DONE:
            return state.set("listRoles", action.data)
        default:
            return state
    }
}
export default BaoCaoReducer