// import { SagaIterator } from 'redux-saga';
import { call, put, takeEvery } from 'redux-saga/effects';

import { LOGIN_USER, LOGOUT_USER } from './constants';
import { loginDone, logoutDone } from './action';
import { loginApi, logoutApi } from './helpers';
import { toast } from 'react-toastify';

function* loginSaga(action: any) {
  const res = yield call(loginApi,action.data);
  if(res.error === 200 ){
    action.onSuccess(res.data)   
    yield put(loginDone(res.data));
  } else {
    action.onError(res.messages)
  }
}

function* logoutSaga(action: any) {
  const res = yield call(logoutApi,action.data);
  if(res.error === 200 ){
    toast.success("Đăng xuất thành công!")
    yield put(logoutDone())
  } else {
    action.onError(res.messages)
  }
}


export default function*() {
  yield takeEvery(LOGIN_USER, loginSaga);
  yield takeEvery(LOGOUT_USER, logoutSaga);
}
