// import { SagaIterator } from 'redux-saga';
import { toast } from 'react-toastify';
import { call, put, takeEvery } from 'redux-saga/effects';
import { logoutDone } from '../auth/action';
import { getAllRolesDone, getAllSubRolesDone } from './action';
import { CREATE_ROLE, GET_ALL_ROLES, GET_ALL_SUB_ROLES, GET_DETAIL_ROLES, UPDATE_ROLE } from './constants';
import { createRoleApi, getAllRolesApi, getAllSubRolesApi, getDetailRolesApi, updateRoleApi } from './helpers';

function* takeGetAllRolesSaga(action: any) {
  const res = yield call(getAllRolesApi,action.data);
  if(res.data && res.data.list ){
    action.onSuccess(res.data)
    yield put(getAllRolesDone(res.data));
  } else {
    if(res.error === 2){
      yield put(logoutDone())
    }
    toast.error(res.messages)
  }
}

function* takeGetAllSubRolesSaga(action: any) {
    const res = yield call(getAllSubRolesApi,action.data);
    if(res.data && res.data.list ){
      action.onSuccess(res.data)
      yield put(getAllSubRolesDone(res.data));
    } else {
      if(res.error === 2){
        yield put(logoutDone())
      }
      toast.error(res.messages)
    }
}

function* takeGetDetailRoleSaga(action: any) {
  const res = yield call(getDetailRolesApi,action.data);
  if(res.data && res.data.list ){
    action.onSuccess(res.data)
    yield put(getAllSubRolesDone(res.data));
  } else {
    if(res.error === 2){
      yield put(logoutDone())
    }
    toast.error(res.messages)
  }
}

function* createRoleSaga(action: any) {
  const res = yield call(createRoleApi,action.data);
  if(res.error === 200 ) action.onSuccess(res.data)
  else {
    toast.error(res.messages)
    if(res.error === 2){
      yield put(logoutDone())
    }
  }
}

function* updateRoleSaga(action: any) {
const res = yield call(updateRoleApi,action.data);
  if(res.error === 200 ) action.onSuccess(res.data)
  else {
    if(res.error === 2){
      yield put(logoutDone())
    }
    toast.error(res.messages)
  }
}

export default function*() {
  yield takeEvery(GET_ALL_ROLES, takeGetAllRolesSaga);
  yield takeEvery(GET_ALL_SUB_ROLES, takeGetAllSubRolesSaga);
  yield takeEvery(GET_DETAIL_ROLES, takeGetDetailRoleSaga);
  yield takeEvery(CREATE_ROLE, createRoleSaga);
  yield takeEvery(UPDATE_ROLE, updateRoleSaga);
}
