import {CREATE_DETAIL_REPORT, CREATE_REPORT, GET_ALL_REPORT, GET_ALL_REPORT_DONE, GET_DETAIL_REPORT, SEARCH_USER, UPDATE_REPORT} from './constants';
export const getAllReport = (data: any, onSuccess: any, onError: any) => ({
    type: GET_ALL_REPORT,
    data,
    onSuccess,
    onError
})

export const getAllReportDone = (data: any) => ({
    type: GET_ALL_REPORT_DONE,
    data
})

export const getDetailReport = (data: any, onSuccess: any, onError: any) => ({
    type: GET_DETAIL_REPORT,
    data,
    onSuccess,
    onError
})

export const createReport = (data: any, onSuccess: any, onError: any) => ({
    type: CREATE_REPORT,
    data,
    onSuccess,
    onError
})

export const createDetailReport = (data: any, onSuccess: any, onError: any) => ({
    type: CREATE_DETAIL_REPORT,
    data,
    onSuccess,
    onError
})

export const updateReport = (data: any, onSuccess: any, onError: any) => ({
    type: UPDATE_REPORT,
    data,
    onSuccess,
    onError
})

export const searchUser = (data: any, onSuccess: any, onError: any) => ({
    type: SEARCH_USER,
    data,
    onSuccess,
    onError
})