import { get } from 'lodash';
// import { actionChannel } from 'redux-saga/effects';

import { sapApiMap } from '../../utils/apisMap';
import { sapApi } from '../../utils/request';

export async function getAllRolesApi(payload: Partial<any>): Promise<any> {
  const res = await sapApi.get(sapApiMap.getting_roles_page+`?pageOffset=${payload.pageNumber}&pageSize=${payload.pageSize}`);
  if (get(res, 'status') === 200) return res.data;
  return {
    messages: "Đã xảy ra lỗi! Vui lòng thử lại"
  }
}

