import {LOGIN_USER, LOGIN_DONE, LOGOUT_USER, LOGOUT_USER_DONE} from './constants';
export const login = (data: any, onSuccess: any, onError: any) => ({
    type: LOGIN_USER,
    data,
    onSuccess,
    onError
})

export const loginDone = (data: any) => ({
    type: LOGIN_DONE,
    data,
})

export const logout = (data: any) => ({
    type: LOGOUT_USER,
    data,
})

export const logoutDone = () => ({
    type: LOGOUT_USER_DONE,
})