// import { SagaIterator } from 'redux-saga';
import { call, put, takeEvery } from 'redux-saga/effects';

import { CREATE_DETAIL_REPORT, CREATE_REPORT, GET_ALL_REPORT, GET_DETAIL_REPORT, SEARCH_USER, UPDATE_REPORT } from './constants';
import { getAllReportDone } from './action';
import { createDetailReportApi, createReportApi, getAllReportApi, getDetailReportApi, searchUserApi, updateReportApi } from './helpers';
import { logoutDone } from '../auth/action';
import { toast } from 'react-toastify';

function* takeGetReportSaga(action: any) {
  const res = yield call(getAllReportApi,action.data);
  if(res.data && res.data.list ){
    action.onSuccess(res.data)
    yield put(getAllReportDone(res.data));
  } else {
    if(res.error === 2){
      yield put(logoutDone())
    }
    toast.error(res.messages)
  }
}

function* takeGetDetailReportSaga(action: any) {
    const res = yield call(getDetailReportApi,action.data);
    if(res.data && res.data.list ){
      action.onSuccess(res.data)
      yield put(getAllReportDone(res.data));
    } else {
      if(res.error === 2){
        yield put(logoutDone())
      }
      toast.error(res.messages)
    }
}

function* createReportSaga(action: any) {
  const res = yield call(createReportApi,action.data);
  if(res.error === 200 ) action.onSuccess(res.data)
  else {
    if(res.error === 2){
      yield put(logoutDone())
    }
    toast.error(res.messages)
  }
}

function* createDetailReportSaga(action: any) {
    const res = yield call(createDetailReportApi,action.data);
    if(res.error === 200 ) action.onSuccess(res.data)
    else {
      if(res.error === 2){
        yield put(logoutDone())
      }
      toast.error(res.messages)
    }
}

function* updateReportSaga(action: any) {
  const res = yield call(updateReportApi,action.data);
  if(res.error === 200 ) action.onSuccess(res.data)
  else {
    if(res.error === 2){
      yield put(logoutDone())
    }
    toast.error(res.messages)
  }
}

function* searchUserSaga(action: any) {
  const res = yield call(searchUserApi,action.data);
  action.onSuccess(res.data)
}

export default function*() {
  yield takeEvery(GET_ALL_REPORT, takeGetReportSaga);
  yield takeEvery(GET_DETAIL_REPORT, takeGetDetailReportSaga);
  yield takeEvery(CREATE_REPORT, createReportSaga);
  yield takeEvery(CREATE_DETAIL_REPORT, createDetailReportSaga);
  yield takeEvery(UPDATE_REPORT, updateReportSaga);
  yield takeEvery(SEARCH_USER, searchUserSaga);
}
