import { get } from 'lodash';
// import { actionChannel } from 'redux-saga/effects';

import { sapApiMap } from '../../utils/apisMap';
// import HttpRequestError from '../../utils/HttpRequetsError';
import { sapApi } from '../../utils/request';

export async function getAllUserApi(payload: Partial<any>): Promise<any> {
  const res = await sapApi.get(sapApiMap.getting_all_users+`?pageOffset=${payload.pageNumber}&pageSize=${payload.pageSize}`);
  if (get(res, 'status') === 200) return res.data;
  return {
    messages: "Đã xảy ra lỗi! Vui lòng thử lại"
  }
}