import { SagaIterator } from 'redux-saga';
import { all, call } from 'redux-saga/effects';
import takeGetReportSaga from './BaoCaoLamViec/saga';
import loginSaga from './auth/saga';
import roleSaga from './Roles/saga';
import userSaga from './User/saga';

export default function* rootSagas(): SagaIterator {
  yield all([
    call(takeGetReportSaga),
    call(loginSaga),
    call(roleSaga),
    call(userSaga),
  ]);
}
